export function setOption5(continentStatisNow){
	
	let data = [];
	for(let key in continentStatisNow.statis){
		data.push({
			value:continentStatisNow.statis[key],
			name:key
		})
	}
	
	let option={
    color: ["#1089E7", "#F57474", "#56D0E3", "#F8B448", "#8B78F6","#f8142b","#3ff806"],
    tooltip: {
      trigger: "item",
      formatter: "{a} <br/>{b}: {c} ({d}%)"
    },

    legend: {
      bottom: "0%",
      // 
      itemWidth: 10,
      itemHeight: 10,
      //  12px
      textStyle: {
        color: "rgba(255,255,255,.5)",
        fontSize: "12"
      }
    },
    series: [
      {
        name: "",
        type: "pie",
        // radius
        // radius  
        radius: ["40%", "60%"],
        center: ["50%", "40%"],
        avoidLabelOverlap: false,
        // 
        label: {
          show: false,
          // position: "center"
        },
        // 
        labelLine: {
          show:false
        },
        data: data
      }
    ]
  }
	return option;
}
