export function worldMapOption(){
	let option = {
		    // 
		    tooltip: {
		        // 
		        trigger: 'item',
		        // 
		        formatter: function (params) {
		            return params.name + " : " + (params.value ? params.value : 0);
		        }
		    },
		    // 
		    visualMap: {
		        // piecewisecontinuous
		        type: 'piecewise',
		        pieces: [
		            { max: 0, label: '0', color: '#eee' },
		            { min: 1, max: 499, label: '1-499', color: '#fff7ba' },
		            { min: 500, max: 4999, label: '500-4999', color: '#ffc24b' },
		            { min: 5000, max: 9999, label: '5000-9999', color: '#ff7c20' },
		            { min: 10000, max: 100000, label: '1-10', color: '#fe5e3b' },
		            { min: 100000, max: 500000, label: '10-50', color: '#e2482b' },
		            { min: 500000, label: '50', color: '#b93e26' },
		        ],
		        itemHeight: 10,
		        itemWidth: 10,
		        inverse: true,
				bottom:"20%",
				textStyle: {
				  color: "rgba(255,255,255,.5)",
				  fontSize: "12"
				}
		    },
		    // 
		    series: [{
		        // 
		        name: '',
		        // 
		        data: '',
		        // 
		        type: 'map',
		        // 
		        mapType: 'world',
		        // 
		        nameMap: '',
		        // 
		        itemStyle: {
		            emphasis: {
		                areaColor: '#c9ffff',
		                label: {
		                    show: false
		                }
		            }
		        },
		        // 
		        layoutCenter: ['48%','50%'],
		        // 
		        layoutSize: "80%",
		    }]
		};
	return option
}
export function setForeignData(foreignList){
	let confirmData = [];
	let nowConfirmData = [];
	let foreignData = {};
	for(let value of foreignList){
		confirmData.push({name:value.name,value:value.confirm})
		nowConfirmData.push({name:value.name,value:value.nowConfirm})
	}
	foreignData = {confirmData,nowConfirmData}
	return foreignData
}